@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";
:root {
  --background-color-main: #0e0e0e;
  --font-cards: "Montserrat", sans-serif;
  --color-title-cards: #dc56c5;
}

ul {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

.main {
  height: auto;
  background-image: url("background-image-main.132dd068.png");
  background-color: var(--background-color-main);
  padding: 20px;
}

.cards-event {
  grid-template-columns: repeat(2, 135px);
  justify-content: center;
  gap: 32px 20px;
  display: grid;
}

.card-container {
  width: 135px;
  height: 208px;
  border-radius: 35% 5px;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 10px 0;
  display: flex;
}

.card-container:hover {
  transition: transform .3s, box-shadow .3s;
  transform: scale(1.2);
  box-shadow: 0 0 20px #f80bfd;
}

.img-card {
  width: 125px;
  height: 160px;
  object-fit: cover;
  border-radius: 50px 0;
}

.info-events-card {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.title-event {
  font-family: var(--font-cards);
  letter-spacing: 0;
  color: var(--color-title-cards);
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 17px;
}

.data-event {
  font-family: var(--font-cards);
  color: #fff;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
}

.location-event a .svg-pin-location {
  width: 6px;
  height: 9px;
}

.location-event a {
  font-family: var(--font-cards);
  color: #fff;
  text-align: center;
  flex-direction: row;
  gap: 3px;
  font-size: 12px;
  font-weight: 600;
  text-decoration: none;
  display: flex;
}

.pagination {
  justify-content: center;
  align-items: center;
  padding: 60px 47px;
  display: flex;
}

.list-pages {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.page-number {
  width: 30px;
  height: 30px;
  color: #fff;
  letter-spacing: 0;
  text-align: center;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 0 10px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  display: flex;
}

.page-number:hover {
  cursor: pointer;
  background: #f80bfd;
  border-radius: 50%;
}

#active {
  background: #f80bfd;
  border-radius: 50%;
}

@media screen and (min-width: 768px) {
  main {
    padding: 32px;
  }

  .cards-event {
    grid-template-columns: repeat(4, 152px);
    justify-content: center;
    gap: 40px 32px;
    display: grid;
  }

  .card-container {
    width: 152px;
    height: 223px;
    gap: 8px;
    padding: 10px 0;
  }

  .img-card {
    width: 152px;
    height: 211px;
    border-radius: 42% 5px;
  }

  .title-event {
    font-size: 16px;
    font-weight: 700;
    line-height: 19.5px;
  }

  .data-event {
    font-size: 16px;
    font-weight: 400;
  }

  .location-event a {
    font-size: 14px;
    font-weight: 600;
  }
}

@media screen and (min-width: 1158px) {
  main {
    padding: 0 16px 60px;
  }

  .cards-event {
    grid-template-columns: repeat(4, 288px);
    justify-content: center;
    gap: 40px 32px;
    display: grid;
  }

  .card-container {
    width: 288px;
    height: 320px;
    gap: 8px;
    padding: 10px 0;
  }

  .img-card {
    width: 267px;
    height: 220px;
    border-radius: 39% 5px;
  }

  .title-event {
    font-size: 16px;
    font-weight: 700;
    line-height: 19.5px;
  }

  .data-event {
    font-size: 16px;
    font-weight: 400;
  }

  .location-event a {
    font-size: 14px;
    font-weight: 600;
  }
}

